<template lang="pug">
  .approve-dialog
    el-dialog(:title="title", v-model="dialogVisible",  @open="onDialogOpen")
      el-form(:model='approve', :rules="rules", ref="approveForm", :label-width='formLabelWidth')
        el-form-item(prop="name", label='')
          el-input(v-model='approve.name', auto-cameomplete='off', placeholder='')
        el-form-item(prop="type", label='')
          el-radio.radio(v-model='approve.type', label='') 
          el-radio.radio(v-model='approve.type', label='',v-if="!eaashow") 
        el-form-item.is-required(prop="relationProducts", label='',v-if="!eaashow")
          input(type="hidden", v-model="approve.relationProducts")
          .relation-products-tag(v-show="approve.relationProducts && approve.relationProducts.length")
            | 
            span(v-for="r in approve.relationProducts")
              | {{r.name}}
              i.icon-batonx.icon-close(@click="removeRelationProduct(r)")
          product-list(:on-change="onProductCheckChange", :checked-list="approve.relationProducts")
        el-form-item(prop="relationProducts",label="")
          el-radio.radio(label="B",v-if="eaashow")
        el-form-item.is-required.flows.custom-error(prop="flowList", label='')
          .flow(v-for="(flow, index) in approve.flowList", :class="{'has-error': flow.hasError}")
            el-select.mr5(style="width:130px;", v-model="flow.department", placeholder="")
              el-option(v-for="d in departments", :value="d.value", :label="d.name")
            el-select.mr5(style="width:130px;", v-model="flow.job", placeholder="")
              el-option(v-for="j in filterJobs(flow)", :value="j.value", :label="j.name")
            el-select(style="width:130px;", v-model="flow.approver", placeholder="")
              el-option(v-for="a in filterApprovers(flow)", :value="a.value", :label="a.name")
            i.icon-batonx.icon-close(@click="removeFlow(index)", v-show="approve.flowList.length > 1")
          input(type="hidden", v-model="approve.flowList")
        el-form-item(label='')
          el-button(type="text", @click="addFlow")
            i.icon-batonx.icon-plus
            | 
      .dialog-footer(slot="footer")
        el-button(type="primary", size="small", @click='approveSave') 
        el-button(type='gray', size="small", @click='dialogVisible = false') 
</template>

<script>
import ProductList from '@/components/ProductList.vue'
import {
  merge,
  mergeWith,
  uniqueId,
  filter,
  find,
  map,
  includes,
  isUndefined,
  remove
} from 'lodash'
import {
  validateArray,
  validateArrayDeep
} from '@/common/validator.js'
import {
  mergeArrayCover
} from '@/common/merge-rules.js'

export default {
  components: {
    ProductList
  },
  props: ['eaaShow'],
  methods: {
    open(approve = {
      id: null,
      name: '',
      type: '',
      flowList: [],
      relationProducts: []
    }) {
      this.dialogVisible = true
      this.$nextTick(() => {
        this.title = approve.id ? '' : ''
        setTimeout(() => {
          this.approve = mergeWith({}, this.approve, approve, mergeArrayCover)
        })
      })
    },

    onDialogOpen() {
      this.$nextTick(() => {
        this.$refs.approveForm.resetFields()
        setTimeout(() => {
          if (!this.approve.id) {
            this.approve.flowList.push({
              department: '',
              job: '',
              approver: '',
              hasError: false
            })
          }
        })
      })
    },

    removeRelationProduct(product) {
      this.approve.relationProducts = remove(this.approve.g, v => v.name !== product.name)
    },

    onProductCheckChange(products) {
      this.approve.relationProducts = merge([], products)
    },

    approveSave() {
      this.$refs.approveForm.validate(valid => {
        if (valid) {
          this.dialogVisible = false
          if (!this.approve.id) this.approve.id = uniqueId('approve_')
          this.$emit('approve-save', this.approve)
        }
      })
    },

    removeFlow(index) {
      this.approve.flowList.splice(index, 1)
    },

    addFlow() {
      this.approve.flowList.push({
        hasError: false,
        department: '',
        job: '',
        approver: ''
      })
    },

    filterJobs(flow = {}) {
      const department = find(this.departments, d => d.name === flow.department)
      if (!department) {
        flow.job = ''
        flow.approver = ''
        return []
      }

      const jobs = filter(this.jobs, j => j.department === department.id)
      if (!includes(map(jobs, 'name'), flow.job)) {
        flow.job = ''
        flow.approver = ''
      }
      return jobs
    },

    filterApprovers(flow) {
      const job = find(this.jobs, d => d.name === flow.job)
      if (!job) {
        flow.approver = ''
        return []
      }

      const approvers = filter(this.approvers, j => j.job === job.id)

      // isUndefined flow.approverundefined?hack
      if (!isUndefined(flow.approver) && !includes(map(approvers, 'name'), flow.approver)) {
        flow.approver = ''
      }
      return approvers
    }
  },

  data() {
    return {
      eaashow: this.eaaShow,
      title: '',
      approve: {
        id: null,
        name: '',
        type: '',
        flowList: [],
        relationProducts: []
      },
      departments: [{
        id: 1,
        name: '',
        value: ''
      }, {
        id: 2,
        name: '',
        value: ''
      }, {
        id: 3,
        name: '',
        value: ''
      }, {
        id: 4,
        name: '',
        value: ''
      }],
      jobs: [{
        id: 1,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 2,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 3,
        department: 1,
        name: '',
        value: ''
      }, {
        id: 4,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 5,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 6,
        department: 2,
        name: '',
        value: ''
      }, {
        id: 7,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 8,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 9,
        department: 3,
        name: '',
        value: ''
      }, {
        id: 10,
        department: 4,
        name: '',
        value: ''
      }, {
        id: 11,
        department: 4,
        name: '',
        value: ''
      }, {
        id: 12,
        department: 4,
        name: '',
        value: ''
      }],
      approvers: [{
        name: '',
        value: '',
        job: 1
      }, {
        name: '',
        value: '',
        job: 2
      }, {
        name: '',
        value: '',
        job: 3
      }, {
        name: '',
        value: '',
        job: 4
      }, {
        name: '',
        value: '',
        job: 5
      }, {
        name: '',
        value: '',
        job: 6
      }, {
        name: '',
        value: '',
        job: 7
      }, {
        name: '',
        value: '',
        job: 8
      }, {
        name: '',
        value: '',
        job: 9
      }, {
        name: '',
        value: '',
        job: 10
      }, {
        name: '',
        value: '',
        job: 11
      }, {
        name: '',
        value: '',
        job: 12
      }],
      rules: {
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        type: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        relationProducts: [{
          validator: validateArray,
          fieldName: '',
          trigger: 'change'
        }],
        flowList: [{
          validator: validateArrayDeep,
          fieldName: '',
          arrayKeys: {
            department: {
              required: true,
              message: ''
            },
            job: {
              required: true,
              message: ''
            },
            approver: {
              required: true,
              message: ''
            }
          },
          trigger: 'blur'
        }]
      },
      formLabelWidth: '120px',
      dialogVisible: false
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/_vars.scss';
.approve-dialog {
  .flows {
    counter-reset: flows 0;
    .flow {
      counter-increment: flows;
      &:before {
        content: counter(flows) '. ';
      }
      .icon-close {
        display: inline-block;
        color: #c9cdd1;
        cursor: pointer;
        margin-left: 5px;
        font-size: 12px;
        &:hover {
          color: $color-blue;
        }
      }
    }
  }
  .relation-products-tag {
    font-size: 12px;
    span {
      padding: 5px 8px;
      border-radius: 3px;
      background: $border-color;
      margin-right: 10px;
      display: inline-block;
      line-height: 1;
      .icon-close {
        cursor: pointer;
        color: #929aa3;
        margin-left: 10px;
        display: inline-block;
        transform: scale(.7);
        &:hover {
          color: $color-blue;
        }
      }
    }
  }
}
</style>



