<template lang="pug">
.box.mt20
  .box-header 
  .box-content.accounts
    el-row(:gutter="20")
      el-col(:span="8", v-for="account in checkedAccounts")
        .account-card
          .operations
            i.icon-batonx.icon-edit(@click="editAccount(account)")
            i.icon-batonx.icon-close(@click="deleteAccount(account)")
          table
            tr
              th 
              td.bd {{account.type}}
            tr
              th 
              td {{account.name}}
            tr
              th 
              td.em {{account.bankNum}}
            tr
              th 
              td {{account.bank}}
      el-col(:span="8")
        .account-card.add-new-account(@click="addCheckedAccount")
  el-dialog(title="", v-model='editAccountVisible', @open="onAccountDialogOpen")
    el-form(:model='activeAccount', ref="accountForm", :rules="rules", :label-width='formLabelWidth')
      el-form-item(prop="type", label='')
        el-select(v-model='activeAccount.type', placeholder='')
          el-option(label='', value='')
          el-option(label='', value='')
          el-option(label='', value='')
      el-form-item(prop="belongto", label='')
        el-input(v-model='activeAccount.belongto', auto-complete='off', placeholder='')
      el-form-item(prop="name", label='')
        el-input(v-model='activeAccount.name', auto-complete='off', placeholder='')
      el-form-item(prop="bankNum", label='')
        el-input(v-model='activeAccount.bankNum', auto-complete='off', placeholder='')
      el-form-item(prop="bank", label='')
        el-input(v-model='activeAccount.bank', auto-complete='off', placeholder='')
    .dialog-footer(slot="footer")
      el-button(type="primary", size="small", @click='activeAccountSave') 
      el-button(type='gray', size="small", @click='editAccountVisible = false') 
  el-dialog.accounts-list(title='', v-model='accountListVisible', size="large", @open="onAccountListOpen", @close="onAccountListClose")
    .box
      .box-tab-header
        el-button(type="primary", size="small", @click="addAccount")
          i.icon-batonx.icon-plus
          | 
      .filters
        el-input(placeholder='', icon='search', v-model.lazy='filter.name')
    el-table(:data='filterAccountList', ref="accountsTable", @selection-change="checkAccount")
      el-table-column(type="selection", width="45")
      el-table-column(property="type", label='')
      el-table-column(property='belongto', label='')
      el-table-column(property='name', label='')
      el-table-column(property='bankNum', label='')
      el-table-column(property='bank', label='', width="200")
      el-table-column(label='')
        template(scope="scope")
          i.icon-batonx.icon-edit(@click="editAccount(scope.row)")
    .dialog-footer(slot="footer")
      el-button(type="primary", size="small", @click='accountListVisible = false') 
</template>

<script>
import {
  MessageBox
} from 'element-ui'
import {
  filter,
  uniqueId,
  remove,
  find,
  each,
  merge,
  map,
  includes
} from 'lodash'

export default {
  props: {
    passedAccounts: {
      type: Array,
      default () {
        return []
      }
    }
  },
  watch: {
    passedAccounts: {
      immediate: true,
      handler() {
        this.accounts = merge([], this.passedAccounts)
      }
    }
  },
  methods: {
    deleteAccount(account) {
      MessageBox.confirm(', ?', '', {
        type: 'warning'
      }).then(() => {
        this.accounts = remove(this.accounts, function(v) {
          return v.id !== account.id
        })
      })
    },
    addAccount() {
      this.activeAccount = {
        type: '',
        name: '',
        belongto: '',
        checked: false,
        bankNum: '',
        bank: ''
      }
      this.editAccountVisible = true
    },

    editAccount(account) {
      this.editAccountVisible = true
      this.$nextTick(() => {
        this.activeAccount = merge({}, account)
      })
    },

    activeAccountSave() {
      this.$refs.accountForm.validate((valid) => {
        if (valid) {
          const account = find(this.accounts, v => this.activeAccount.id === v.id)
          if (account) { // 
            merge(account, this.activeAccount)
            this.editAccountVisible = false
          } else { // 
            this.activeAccount.id = uniqueId()
              // this.activeAccount.checked = false
            this.accounts.unshift(this.activeAccount)
            this._tableCheckedUpdate()
            this.editAccountVisible = false
          }
        }
      })
    },

    addCheckedAccount() {
      this.accountListVisible = true
    },

    _tableCheckedUpdate() {
      this.$nextTick(() => {
        if (!this.$refs.accountsTable) return
        each(this.accounts, (v) => {
          this.$refs.accountsTable.toggleRowSelection(v, v.checked)
        })
      })
    },

    onAccountDialogOpen() {
      this.$nextTick(() => {
        this.$refs.accountForm.resetFields()
      })
    },

    onAccountListOpen() {
      this._tableCheckedUpdate()
    },

    onAccountListClose() {
      const ids = map(this.accountListChecked, 'id')
      each(this.accounts, v => {
        v.checked = includes(ids, v.id)
      })
    },

    checkAccount(val) {
      this.accountListChecked = val
    }
  },

  computed: {
    checkedAccounts() {
      return filter(this.accounts, v => v.checked)
    },
    filterAccountList() {
      if (!this.accounts.length) return this.accounts
      this._tableCheckedUpdate()
      return filter(this.accounts, v => {
        return ~v.name.indexOf(this.filter.name) || ~v.bankNum.indexOf(this.filter.name)
      })
    }
  },

  data() {
    return {
      filter: {
        name: ''
      },
      rules: {
        belongto: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        bankNum: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        bank: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        type: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        relationProducts: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      accounts: merge([], this.passedAccounts),
      formLabelWidth: '120px',
      editAccountVisible: false,
      accountListVisible: false,
      activeAccount: {},
      accountListChecked: []
    }
  }
}
</script>



