<template lang="pug">
.asset-form
  .box
    .box-header
      | 
    .box-content
      el-form(:model="demo", :rules="rules", ref="assetForm", label-width="120px")
        el-row
          el-col(:span="12")
            el-form-item(label="", prop="name")
              el-input(type="text", placeholder="", v-model="demo.name")
            el-form-item(label="", prop="amount")
              el-input(type="text", placeholder="", v-model="demo.sale")
            el-form-item(label="", prop="ratio")
              el-input(type="text", placeholder="", v-model="demo.productName")
            el-form-item(label="", prop="startDate")
              el-input(type="text", placeholder="", v-model="demo.productCode")

            el-form-item(label="", prop="term")
              el-input(type="text", placeholder="", v-model="demo.issueRate")
                template(slot="append") %
            el-form-item(label="", prop="payFrequency")
              el-date-picker(type="date", :value="demo.startDate", @input="handleStartDate", placeholder="")
              //- el-input(type="text", placeholder="", v-model="demo.startDate")
            el-form-item(label="", prop="assetType")
              el-date-picker(type="date", :value="demo.endDate", @input="handleStartDate", placeholder="")
              //- el-input(type="text", placeholder="", v-model="demo.endDate")
            el-form-item(label="", prop="publisher")
              el-input(type="text", placeholder="", v-model="demo.term")
                template(slot="append") 
            //- el-form-item(label="", prop="trustee")
            //-   el-input(type="text", placeholder="", v-model="demo.trustee")
          el-col(:span="12")
            el-form-item(label="", prop="shortName")
              el-input(type="text", placeholder="", v-model="demo.planAmount")
                template(slot="append") 
            el-form-item(label="", prop="issueAmount")
              el-input(type="text", placeholder="", v-model="demo.actualAmount")
                template(slot="append") 
            el-form-item(label="", prop="rate")
              //- el-input(type="text", placeholder="", v-model="demo.riskRating")
              //-   template(slot="append") %
              el-select(v-model="risksRating",placeholder="")
                el-option(v-for="risk in risks",:key="risk.value",:label="risk.label",:value="risk.value")
            el-form-item(label="", prop="endDate")
              el-date-picker(type="date", :value="demo.sjDate", @input="handleEndDate", placeholder="")
            el-form-item(label="", prop="riskLevel")
              el-date-picker(type="date", :value="demo.xjDate", @input="handleEndDate", placeholder="")
              //- el-select(v-model="demo.riskLevel")
              //-   el-option(v-for="o in riskLevels", :label="o.name", :value="o.value")
            el-form-item(label="", prop="payDate")
              el-input(type="text", placeholder="", v-model="demo.raiseTerm")
                template(slot="append") 
              //- el-date-picker(type="date",  placeholder="", :value="demo.payDate", @input="handlePayDate",)
            el-form-item(label="", prop="credit")
              el-input(type="text", placeholder="", v-model="demo.balanceTerm")
            el-form-item(label="", prop="gurantee")
              el-input(type="text", placeholder="", v-model="demo.yearNum")
            //- el-form-item(label="", prop="flow")
            //-   el-select(v-model="demo.flow")
                el-option(v-for="o in flows", :label="o.name", :value="o.value")
  .box.mt20
    .box-header 
    .box-content
      .cost-table
        table
          thead
            tr
              th 
              th 
              th 
              th 
              th 
              th 
              th 
              th 
              th 
          tbody
            tr(v-for="costList in costLists")
              td
                el-input(v-model="costList.charge")
              td
                el-input(v-model="costList.costType")
              td
                el-input(v-model="costList.chargingType")
              td
                el-input(v-model="costList.rate")
              td
                el-input(v-model="costList.chargingDays")
              td
                el-input(v-model="costList.costBase")
              td
                el-input(v-model="costList.payment")
              td
                el-input(v-model="costList.paymentDate")
              td
                el-input(v-model="costList.chargingstart")
            tr
              td.addMore(colspan="9")
                a(@click="addMore(costList)")
                  i.icon-batonx.icon-plus
                  span 
  account-card(:passed-accounts='demo.accounts')
  .box.mt20
    .box-header 
    .box-content
      .examine
        table(width="100%")
          thead
            tr
              th 
              th 
              th 
          tbody
            tr(v-for="Eaadata in filterEaa",v-if="filterEaa.length")
              td {{Eaadata.EaaName}}
              td
                span {{Eaadata.details}}
                i.icon-batonx.icon-shenpi
              td
                span(@click="EditDialog")
                  i.icon-batonx.icon-edit
                  | 
                span(@click="CloseDialog(Eaadata)")
                  i.icon-batonx.icon-close
                  | 
            //- tr(v-if="!Eaadatas.lenght")
            //-   td(colspan="3") 
  el-dialog(title="",v-model="Eaavisibel")
    .box
      .box-tab-header
        el-button.fr(type="primary", size="small", @click="addEaa")
          i.icon-batonx.icon-plus
          | 
      .filters
        el-input(placeholder='', icon='search', v-model.lazy='filter.name')
    el-table(:data='Eaadatas', ref="accountsTable")
      el-table-column(type="selection", width="45")
      el-table-column(property="EaaName", label='')
      el-table-column(property='process', label='')
      //- el-table-column(property='belongto', label='')
      //-   template(scope="scope")
      //-     i.icon-batonx.icon-edit
    .dialog-footer(slot="footer")
      el-button(type="primary", size="small", @click='Eaavisibel = false') 
  approve-dialog(ref="approveVisi",:eaa-show="eaaapp")
  .bottom-buttons
    el-button(type="primary", size="small", @click="submitForm") 
    el-button(type="gray", size="small", @click="cancel") 

</template>

<script>
import {
  MessageBox,
  Message
} from 'element-ui'
import {
  updateCrumbs
} from '@/common/crossers.js'
import AccountCard from '@/components/AccountCard.vue'
import ApproveDialog from '@/components/ApproveDialog.vue'
import moment from 'moment'
import {
  uniqueId,
  remove,
  each,
  filter
} from 'lodash'

export default {
  components: {
    AccountCard,
    ApproveDialog
  },
  mounted() {
    if (this.$route.params.id !== 'add') {
      const name = this.$route.params.id
      updateCrumbs.$emit('update-crumbs', [{
        id: 'assetName',
        name: name
      }])
      this.demo = {
        name: name,
        sale: '',
        productName: 'B',
        productCode: 'YZ0408~YZ0413',
        issueRate: '6.5%',
        startDate: '2017-01-04',
        endDate: '2017-04-03',
        term: 90,
        planAmount: '124,534,253.00',
        actualAmount: '124,534,253.00',
        riskRating: '',
        sjDate: '2017-01-01',
        xjDate: '2017-01-02',
        raiseTerm: '1',
        balanceTerm: '12:00',
        yearNum: '365',
        // payFrequency: '',
        // assetType: '',
        // publisher: '',
        // trustee: '',
        // shortName: name,
        // issuePlatform: '',
        // rate: 8,

        // riskLevel: 'L',
        // payDate: '2017-04-03',
        // credit: '',
        // gurantee: name,
        // flow: 'N',
        accounts: [{
          id: uniqueId(),
          checked: true,
          type: '',
          belongto: name,
          name: name,
          bankNum: '2310 8372 6532 098',
          bank: ''
        }]
      }
    }
  },

  methods: {
    addEaa() {
      this.$refs.approveVisi.open()
    },
    EditDialog() {
      this.Eaavisibel = true
      this.$nextTick(() => {
        each(this.Eaadatas, v => {
          if (v.EaaName === this.EaaDefault.EaaName) {
            this.$refs.accountsTable.toggleRowSelection(v, true)
          }
        })
      })
    },
    CloseDialog(eaa) {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.Eaadatas = remove(this.Eaadatas, v => {
          return eaa.EaaName !== v.EaaName
        })
      })
    },
    addMore(data) {
      this.costLists.push(this.emptyObj)
    },
    submitForm() {
      Message.info('Demo')
    },
    cancel() {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.$router.back()
      })
    },
    handleStartDate(value) {
      if (!value) return
      this.demo.startDate = moment(value).format('YYYY-MM-DD')
    },
    handleEndDate(value) {
      if (!value) return
      this.demo.endDate = moment(value).format('YYYY-MM-DD')
    },
    handlePayDate(value) {
      if (!value) return
      this.demo.payDate = moment(value).format('YYYY-MM-DD')
    }
  },
  computed: {
    filterEaa() {
      return filter(this.Eaadatas, v => {
        return ~v.EaaName.indexOf(this.EaaDefault.EaaName)
      })
    }
  },

  data() {
    return {
      eaaapp: true,
      EaaDefault: {
        EaaName: 'A1',
        process: ''
      },
      Eaavisibel: false,
      risksRating: '',
      filter: {
        name: ''
      },
      risks: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }],
      rules: {},
      demo: {
        name: null,
        amount: null,
        ratio: null,
        startDate: null,
        term: null,
        payFrequency: null,
        assetType: null,
        publisher: null,
        trustee: null,
        shortName: null,
        issueAmount: null,
        rate: null,
        endDate: null,
        riskLevel: 'L',
        payDate: null,
        credit: null,
        gurantee: null,
        flow: 'N',
        accounts: []
      },
      pickOpt: {
        format: 'yyyy-MM-dd'
      },
      riskLevels: [{
        name: '',
        value: 'L'
      }, {
        name: '',
        value: 'M'
      }, {
        name: '',
        value: 'H'
      }],
      flows: [{
        name: '',
        value: 'N'
      }, {
        name: '',
        value: 'H'
      }, {
        name: '',
        value: 'L'
      }],
      costLists: [{
        charge: '',
        costType: '',
        chargingType: ' ',
        rate: '0.2%',
        chargingDays: '360',
        costBase: '',
        payment: '  ',
        paymentDate: '2016-8-8    12:00:00 AM ',
        chargingstart: ' 23,533,09.00'
      }, {
        charge: ' ',
        costType: '',
        chargingType: '',
        rate: ' ',
        chargingDays: ' ',
        costBase: ' ',
        payment: '  ',
        paymentDate: '2016-8-8    14:00:00 PM',
        chargingstart: ' 123,533,09.00'
      }],
      emptyObj: {
        charge: '',
        costType: '',
        chargingType: '',
        rate: ' ',
        chargingDays: '',
        costBase: '',
        payment: '',
        paymentDate: '',
        chargingstart: ''
      },
      Eaadatas: [{
        EaaName: 'A1',
        details: '',
        process: '    '
      }, {
        EaaName: 'B1',
        details: '',
        process: ''
      }, {
        EaaName: 'C1',
        details: '',
        process: '  '
      }]
    }
  }
}
</script>

<style lang="scss">
.accounts-list {
  .box {
    border: 0;
    padding: 10px 0;
    .box-tab-header {
      top: 12px;
      right: 0;
    }
    .filters {
      padding: 0;
    }
  }
}

.cost-table {
  table {
    width: 100%;
    tr {
      border-bottom: 1px solid #e2e5e9;
      &:last-child {
        border-bottom: none;
      }
    }
    th {
      padding: 10px 8px;
      background: #c6e3f1;
      font-weight: normal;
      text-align: left;
    }
    td {
      &.addMore {
        padding: 10px 0;
        text-align: center;
        i {
          color: #538cc0;
        }
        span {
          margin-left: 5px;
        }
      }
    }
    .el-input {
      .el-input__inner {
        border: none;
      }
    }
  }
}

.examine {
  padding: 15px;
  table {
    width: 100%;
    th {
      font-weight: normal;
      color: #abb1b8;
      // text-align: left;
    }
    td {
      padding: 10px 0;
      text-align: center;
      i {
        color: #c9cdd1;
      }
      span {
        cursor: pointer;
        margin-right: 10px;
        i {
          display: inline-block;
          margin-right: 5px;
          transform: scale(0.8);
          vertical-align: 0;
          color: #c9cdd1;
        }
      }
    }
  }
}

.box-tab-header {
  top: 15px!important;
}
</style>



